body {
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  color: #000000;
  overflow-x: hidden; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.header-wrapper {
  background-color: antiquewhite;
  max-width: 1400px;
  margin: 0 auto;
  text-align: center; }

html,
body {
  height: 100%;
  background-color: #333333;
  color: #ce0808;
  font-size: 20px; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.fullscreen {
  min-height: 100vh;
  background: url("https://www.interfax.ru/aspimg/46245p.png") center/cover no-repeat no-repeat fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative; }
  .fullscreen::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); }
  .fullscreen:hover .friday {
    box-shadow: 0 -10px 30px 10px rgba(0, 0, 0, 0.2);
    bottom: -25%; }
  .fullscreen:hover .black {
    box-shadow: 0 10px 30px 10px rgba(0, 0, 0, 0.2);
    top: -25%; }
  .fullscreen-one .service__link {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%) rotate(90deg);
    width: 40px;
    height: 40px;
    background-color: #ce0808;
    z-index: 10;
    font-size: 0;
    color: transparent; }
    .fullscreen-one .service__link::after {
      position: absolute;
      content: "\203A";
      width: 40px;
      height: 40px;
      top: 5px;
      left: 0;
      z-index: 20;
      font-size: 30px;
      color: #000000;
      text-align: center;
      transition: 0.2s ease-in-out;
      -webkit-animation: bouncex 0.3s alternate ease infinite;
              animation: bouncex 0.3s alternate ease infinite; }
    .fullscreen-one .service__link:hover::after {
      -webkit-animation: none;
              animation: none; }
  .fullscreen-two {
    background-color: brown;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .fullscreen-three {
    background-color: #e01313;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  .fullscreen-bg {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url("https://www.interfax.ru/aspimg/46245p.png") center/cover no-repeat fixed;
    position: relative; }
    .fullscreen-bg::before {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3); }

.fullscreen__body {
  text-align: center;
  color: #ffffff;
  overflow: hidden;
  flex: 1 1 auto;
  width: 100%;
  min-height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }

.content {
  padding: 40px; }

.black {
  background-color: #000000;
  text-transform: uppercase;
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  min-height: 50vh;
  width: 100%;
  transition: 1s all ease-in-out;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 20px;
  color: #ce0808;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none; }

.friday {
  background-color: #ce0808;
  text-transform: uppercase;
  font-size: 70px;
  line-height: 70px;
  font-weight: 700;
  padding: 20px;
  position: absolute;
  left: 0;
  bottom: 0;
  color: #000000;
  min-height: 50vh;
  width: 100%;
  transition: 1s all ease-in-out;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none; }

.link {
  text-transform: uppercase;
  display: block;
  padding: 10px 25px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
  border: 3px solid #ffffff;
  background-color: #ce0808;
  border-radius: 20px; }
  .link:hover {
    -webkit-animation: shake 0.8s linear infinite;
            animation: shake 0.8s linear infinite; }

@-webkit-keyframes shake {
  0% {
    transform: translate(2px, 1px) rotate(0deg); }
  10% {
    transform: translate(-1px, -2px) rotate(-2deg); }
  20% {
    transform: translate(-3px, 0px) rotate(3deg); }
  30% {
    transform: translate(0px, 2px) rotate(0deg); }
  40% {
    transform: translate(1px, -1px) rotate(1deg); }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg); }
  60% {
    transform: translate(-3px, 1px) rotate(0deg); }
  70% {
    transform: translate(2px, 1px) rotate(-2deg); }
  80% {
    transform: translate(-1px, -1px) rotate(4deg); }
  90% {
    transform: translate(2px, 2px) rotate(0deg); }
  100% {
    transform: translate(1px, -2px) rotate(-1deg); } }

@keyframes shake {
  0% {
    transform: translate(2px, 1px) rotate(0deg); }
  10% {
    transform: translate(-1px, -2px) rotate(-2deg); }
  20% {
    transform: translate(-3px, 0px) rotate(3deg); }
  30% {
    transform: translate(0px, 2px) rotate(0deg); }
  40% {
    transform: translate(1px, -1px) rotate(1deg); }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg); }
  60% {
    transform: translate(-3px, 1px) rotate(0deg); }
  70% {
    transform: translate(2px, 1px) rotate(-2deg); }
  80% {
    transform: translate(-1px, -1px) rotate(4deg); }
  90% {
    transform: translate(2px, 2px) rotate(0deg); }
  100% {
    transform: translate(1px, -2px) rotate(-1deg); } }

@-webkit-keyframes bouncex {
  from {
    transform: translateX(-3px); }
  to {
    transform: translateX(3px); } }

@keyframes bouncex {
  from {
    transform: translateX(-3px); }
  to {
    transform: translateX(3px); } }
